import { Injectable } from '@angular/core';
import { Producer } from '../models/producer';

@Injectable({ providedIn: 'root'})
export class HttpService{
    producers: Producer[] = [
        { 
            name: "Пицца лисица",
            logoPath: '../assets/img/pzz-logo.png',            
            link: "https://pzz.by/"
        }
    ]

    getProducers(): Producer[] {
        return this.producers;
    }
}